<template>
    <div>
        <h3>Carriers Add Page</h3>
        <b-form class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <b-form-group label="Name" label-for="inputName">
                <b-form-input
                    v-model="$v.name.$model"
                    id="inputName"
                    label="Name"
                    v-bind:class="{ 'is-invalid': $v.name.$error }">
                ></b-form-input>
                <b-form-invalid-feedback>
                    This field is required
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="Account number" label-for="inputAccountNumber">
                <b-form-input
                    id="inputAccountNumber"
                    v-model="$v.account_number.$model"
                    v-bind:class="{ 'is-invalid': $v.account_number.$error }">
                ></b-form-input>
                <b-form-invalid-feedback>
                    This field is required
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="Phone number" label-for="inputPhoneNumber">
                <b-form-input
                    id="inputPhoneNumber"
                    v-model="$v.phone_number.$model"
                    v-bind:class="{ 'is-invalid': $v.phone_number.$error }">
                ></b-form-input>
                <b-form-invalid-feedback>
                    Phone number must contain 10 digits
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="Email address" label-for="inputEmailAddress">
                <b-form-input
                    id="inputEmailAddress"
                    v-model="$v.email_address.$model"
                    v-bind:class="{ 'is-invalid': $v.email_address.$error }">
                ></b-form-input>
                <b-form-invalid-feedback>
                    Enter a valid email address
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="URL" label-for="inputUrl">
                <b-form-input
                    id="inputUrl"
                    v-model="$v.url.$model"
                    v-bind:class="{ 'is-invalid': $v.url.$error }">
                ></b-form-input>
                <b-form-invalid-feedback>
                    Enter a valid URL
                </b-form-invalid-feedback>
            </b-form-group>
            <b-button type="submit" @click="onSubmit" variant="primary" class="px-4">Add</b-button>
        </b-form>
    </div>
</template>

<script>
import { required, email, url, numeric, minLength, maxLength } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';
const backend = new Backend();

export default {
    name: 'CarriersAddPage',
    data() {
        return {
            name: '',
            account_number: '',
            phone_number: '',
            email_address: '',
            url: '',
            hasBackendError: false,
            successMessage: null,
            errorMessage: null
        };
    },
    validations: {
        name: {
            required
        },
        account_number: {

        },
        phone_number: {
            numeric,
            minLength: minLength(10),
            maxLength: maxLength(10)
        },
        email_address: {
            email
        },
        url: {
            url
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = {
                name: this.name
            };
            backend.postRequest('services/carriers/', data)
                .then((response) => {
                    let addressName = response.data.name;
                    this.hasBackendError = false;
                    this.errorMessage = null;
                    this.successMessage = `${addressName} was added successfully`;
                    this.address = '';
                    setTimeout(() => this.$v.address.$reset(), 0);
                })
                .catch((error) => {
                    this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                        'Something went wrong. Please try again later';
                });
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
